import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%'
    },
    heading: {
        fontSize: theme
            .typography
            .pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0
    },
    secondaryHeading: {
        fontSize: theme
            .typography
            .pxToRem(15),
        color: theme.palette.text.secondary
    }
}));

const NewInfo = props => {
    const classes = useStyles();

    let {ind, expanded, text, titleinfo, handleChange} = props;

    return (
        <li className={'list-info-li'}>
            <img src={props.icon} alt="IconList"/>
            <Accordion
                expanded={expanded === `panel${ind}`}
                onChange={handleChange(`panel${ind}`)}>
                <AccordionSummary
                    expandIcon={< ExpandMoreIcon />}
                    aria-controls="panel1bh-content"
                    id="panel1bh-header">
                    <Typography className={classes.heading}>{titleinfo}</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        {text}
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </li>
    )
}

export default NewInfo;